@import url(https://fonts.googleapis.com/css?family=Roboto);
.App{
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-color:white;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

main{
  flex: 1 1;
  background-color:white;
}

.App-header {
  
  background-color:black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Header_container{
  background-color:rgb(230, 227, 227);
  display: flex;  
  float:right;
  margin:15px;  
}

.logo{
  margin:25px;
  font-family: 'Luckiest Guy', cursive;
  font-size:25px;
}
.App__header{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  background-color:rgb(230, 227, 227);
}
.Header_buttons a{
  font-size:20px;
  
  padding:0.35em 1.2em;
  /* border:0.1em solid black; */
  background-color:rgb(112, 112, 112);
  font-family:'Roboto',sans-serif;
  color:white;
  /* padding:5px; */
  margin:3px 8px;
  text-decoration: none;
}

.Header_buttons{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  font-size:20px;
  font-family:'Roboto',sans-serif;
}




.Header_buttons a:hover{
  font-size: 25px;
  
}

@media (max-width:600px){
  .Header{
    flex-direction: column;
  }
  .Header__logged-in{
    flex-direction: column;
    width:100%;
  }
  .Header a{
    width:100%;
    padding:0px;
    text-align: center;
  }
  .Header__not-logged-in{
    flex-direction: column;
  }

  .Header_buttons{
    display: flex;
    flex-direction: column;
    
    font-size:20px;
    font-family:'Roboto',sans-serif;
  }

}
.LandingPage__welcome{
  font-size: 70px;
  color:white;
  font-family: 'Anton', sans-serif;
}



.LandingPage_Container{
  display:flex;
  justify-content: center;  
  margin:250px auto;    
  background-color:black;
  padding:100px;
}


@media(max-width:600px){
.LandingPage__welcome{
  font-size:30px;
}
.LandingPage_Container{
  margin:120px auto;
  padding:80px;
  
}
}


.Particle{
	position: absolute;
    pointer-events: none;
    left: 0;
    right: 0;
    bottom: 0;
		top: 0;
		
